import React from "react";
import * as s from "./Advert.css";

/**
 *
 * @param {object} props
 * @param {object} props.advert -  
 * @param {string} props.advert.title -  
 * @param {string} props.advert.text -  
 * @param {string} props.advert.phone -  
 * @returns {JSX.Element | null}  
 */
const Advert = props => {
  const { advert } = props;
  if (!advert) {
    return null;
  }
  const { title, text, phone } = advert;

  return (
    <div className={s.root}>
      <h3>{title}</h3>
      <p>{text}</p>
      <a href={`tel:${phone}`}>{phone}</a>
      <div className={s.status}>
        <span className={s.edit} onClick={props.clickEditAdvert}>
          
        </span>
        <span className={s.delete} onClick={props.clickDeleteCallback}>
          
        </span>
      </div>
    </div>
  );
};

export default Advert;
